import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-calatogue',
  templateUrl: './calatogue.component.html',
  styleUrls: ['./calatogue.component.css']
})
export class CalatogueComponent implements OnInit {
  @ViewChild('attentionModal', { static: true })
  public attentionModal: ElementRef<HTMLElement>;
  @ViewChild('graalModal', { static: true })
  public graalModal: ElementRef<HTMLElement>;
  @ViewChild('tarelkaModal', { static: true })
  public tarelkaModal: ElementRef<HTMLElement>;
  @ViewChild('encyclopediaModal', { static: false })
  public encyclopediaModal: ElementRef<HTMLElement>;

  constructor(public router: Router) { }

  ngOnInit() {
    this.attentionModal.nativeElement.style.display = 'block';
  }

  public goToLanding(): void {
    this.router.navigate(['landing']);
  }

  public closeAttentionModal(): void {
    this.attentionModal.nativeElement.style.display = 'none';
  }

  public toggleGraalModal(open: boolean): void {
    this.graalModal.nativeElement.style.display = !open ? 'none' : 'block';
  }

  public toggleTarelkaModal(open: boolean): void {
    this.tarelkaModal.nativeElement.style.display = !open ? 'none' : 'block';
  }

  public toggleEnciclopedia(open: boolean): void {
    this.encyclopediaModal.nativeElement.style.display = !open ? 'none' : 'block';
  }

}
